<template>
  <div class="mapping">
		<v-alert :type="alertType" :message="alertMessage" show-icon v-if="alertShowing"></v-alert>
		<v-row>
			<v-col span="24">
				<div class="search-box" @keyup.enter="enterClick">
					<v-input size="large" placeholder="请输入 EOS 账户" class="search-input" v-model="searchText"></v-input>
					<v-button class="search" type="primary" shape="circle" icon="search" @click="seachClick"></v-button>
				</div>
			</v-col>
		</v-row>
		<v-row v-if="searchStatus === 'query'">
			<v-col span="24">
				<div class="card">
					<v-spin tip="查询中.">
					</v-spin>
				</div>
			</v-col>
		</v-row>
		<template v-else-if="searchStatus === 'success'">
			<v-row type="flex" align="middle">
				<v-col span="10">
					<div class="card">
						<h3 class="h3-red">{{eosAccountName}}</h3>
						<span>{{eosAccountBalance}}</span>
					</div>
				</v-col>
				<v-col span="4">
					<img class="right-arrow" src="../../assets/right-arrow.png"></img>
				</v-col>
				<v-col span="10">
					<div class="card">
						<h3 class="h3-red">{{bosAccountName}}</h3>
						<span>{{bosAccountBalance}}</span>
					</div>
				</v-col>
			</v-row>
			<v-row type="flex" justify="start">
				<v-col span="24">
					<div class="card card-bottom">
						<h3 class="left-h3">Owner</h3>
						<span class="span—permission">{{owner_key}}</span>
						
						<h3 class="left-h3">Active</h3>
						<span class="span—permission">{{active_key}}</span>
					</div>
				</v-col>
			</v-row>
		</template>
		
		<v-row v-else-if="searchStatus === 'fail'">
			<v-col span="24">
				<div class="card">
					<h3>未查询到相关账户</h3>
				</div>
			</v-col>
		</v-row>
		<v-row v-else>
			<v-col span="24">
				<v-button type="primary" @click="scatterLoginClick">Scatter 登录</v-button>
			</v-col>
		</v-row>
		
		<v-row type="flex" justify="start">
			<v-col span="24">
				<div class="card card-bottom">
					<h3 class="left-h3">BOS 快照说明</h3>
					<span class="span-rule">
						时间：<span class="span-rule span-red">2019-01-01 17:50:00</span>
						比例：<span class="span-rule span-red">20 EOS : 1 BOS</span>
						<span>小于 0.5 EOS，则为 0.5 BOS</span>
					</span>
				</div>
			</v-col>
		</v-row>
  </div>
</template>

<script>
export default {
  name: 'mapping',
	data() {
		return {
			alertShowing:false,
			alertType:'error',		// success、error、warning
			alertMessage:'message',		
			searchText:'',
			searchStatus:'default', // default、query、fail、success
			eosAccountName:'',
			bosAccountName:'',
			eosAccountBalance:'',
			bosAccountBalance:'',
			owner_key:'',
			active_key:'',
			isBlockchainEOS:true
		}
	},
	mounted() {
		// 麦子钱包
		var network = ScatterJS.Network.fromJson({
							blockchain:'eos',
							chainId:'aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906',
							host:'eosmainnet.medishares.net',
							port:443,
							protocol:'https'
					});
		if(mathwallet.isMath()){
			var that = this;
			mathwallet.getWalletType().then(function(result){
				if(result != 'EOS'){
					network = ScatterJS.Network.fromJson({
							blockchain:'eos',
							chainId:'d5a3d18fbb3c084e3b1f3fa98c21014b5f3db536cc15d08f9f6479517c6a3d86',
							host:'bosnet.medishares.net',
							port:443,
							protocol:'https'
					});
					that.isBlockchainEOS = false;
				}else{
					that.isBlockchainEOS = true;
				}
				that.scatterConnecting(network);
			});
		}else{
			this.isBlockchainEOS = true;
			this.scatterConnecting(network);
		}

		
	},
	methods:{
		hideAlert(){
			this.alertShowing = false;
		},
		showAlert(type,message){
			this.alertShowing = true;
			this.alertType = type;
			this.alertMessage = message;
		},
		scatterConnecting(network){
			var that = this;
			ScatterJS.connect('BOSMapping', {network} ).then(function(connected){
						if(connected){
							that.scatterLoginClick();
						}else{
							that.showAlert("warning","Scatter connect failed!");
						}
				}).catch(function(e){
					that.showAlert("error",JSON.stringify(e));
				});
		},
		scatterLoginClick(){
			var that = this;
			ScatterJS.login().then(function(result){
				const account = ScatterJS.identity.accounts.find(x => {
					return x.blockchain === 'eos';
				});
				if(that.isBlockchainEOS){
					// Search
					that.searchText = account.name;
					that.seachClick();
				}else{
					that.showAlert("warning","请切换到 EOS 钱包或输入 EOS 账户查询");
				}
			}).catch(function(e){
				that.showAlert("error",JSON.stringify(e));
			});
		},
		enterClick(){
			this.seachClick();
		},
		seachClick(){
			if(this.searchText.length == 0){
				return;
			}
			this.searchStatus = 'query';
			var that = this;
			this.$http({
					method:'get',
					url:'http://market.jiazechen.com/site/jsonp?url=https://api.boslaoge.me/'+this.searchText,
					headers:{
						'Access-Control-Allow-Credentials':true
					}
				}).then(function(response){
					if(Array.isArray(response.data) && response.data.length > 0){
						that.searchStatus = 'success';
						that.eosAccountName = response.data[0].eos_account;
						that.bosAccountName = response.data[0].bos_account;
						
						that.eosAccountBalance = response.data[0].eos_balance;
						that.bosAccountBalance = response.data[0].bos_balance;
						
						that.active_key = response.data[0].active_key;
						that.owner_key = response.data[0].owner_key;
					}else{
						that.searchStatus = 'fail';
						console.log('No Search Result!');
					}
				}).catch(function(e){
						that.searchStatus = 'fail';
						that.showAlert("error","查询 BOS 快照出现网络异常！");
				})
		}
	}
}
</script>

