import Vue from 'vue'
import Router from 'vue-router'

import BOSMapping from './views/BOS/Mapping.vue'
import BOSNewAccount from './views/BOS/NewAccount.vue'

import TelosNewAccount from './views/telos/NewAccount.vue'

import ENUTransfer from './views/enu/Transfer.vue'

import Token from './views/Token.vue'

Vue.use(Router)

export default new Router({
	routes:[
		{
			path:'/bos/mapping',
			name:'mapping',
			component:BOSMapping
		},
		{
			path:'/bos/newaccount',
			name:'bosNewAccount',
			component:BOSNewAccount
		},
		{
			path:'/telos/newaccount',
			name:'telosNewAccount',
			component:TelosNewAccount
		},
		{
			path:'/enu/transfer',
			name:'enuTransfer',
			component:ENUTransfer
		},
		{
			path:'/token',
			name:'token',
			component:Token
		}
	]
})