<template>
	<div class="enuTransfer">
		<v-button type="primary" size="large" @click="transferClick">转账</v-button>
		
	</div>
</template>

<script>
	
	import Enu from 'enujs'
	export default{
		name:'enuTransfer',
		mounted() {
			var network = ScatterJS.Network.fromJson({
								blockchain:'eos',
								chainId:'cf057bbfb72640471fd910bcb67639c22df9f92470936cddc1ade0e2f2e7dc4f',
								host:'enu.mytokenpocket.vip',
								port:80,
								protocol:'http'
						});
			this.scatterConnecting(network);
		},
		methods: {
			scatterConnecting(network){
				var that = this;
				ScatterJS.connect('TelosNewAccount', {network} ).then(function(connected){
							if(connected){
								that.scatterLoginClick();
							}else{
								that.$message.warning("Scatter connect failed!", 2);
							}
					}).catch(function(e){
						that.$message.error(JSON.stringify(e), 2);
					});
			},
			scatterLoginClick(){
				var that = this;
				ScatterJS.login().then(function(result){
					const account = ScatterJS.identity.accounts.find(x => {
						return x.blockchain === 'eos';
					});
					that.loginAccountName = account.name;
					that.loginAccountAuthority = account.authority;
					that.$message.info(JSON.stringify(result),2);
				}).catch(function(e){
					that.$message.error(JSON.stringify(e),2);
				});
			},
			transferClick(){
				
			}
		},
	}
</script>

