<template>
	<div class="newAccount">
		<h2 class="title"> 创建 12 位账户名 </h2>
		<h3 class="subtitle"> {{loginAccountName}} </h3>
		<div class="panel">
			<v-row>
				<v-col span="24" align="left">
					<h4 class="bottom6">BOS 账号名</h4>
					<v-input class="input" size="large" v-model="newAccountName" placeholder="输入12位长度,字母 a-z、数字 0-5"></v-input>
					<h4 class="top12 bottom6">Owner 公钥</h4>
					<v-input class="input" size="large" v-model="ownerPermissionKey" placeholder="输入 Owner 公钥"></v-input>
					<h4 v-if="advancedMode" class="top12 bottom6">Active 公钥</h4>
					<v-input v-if="advancedMode" class="input" size="large" v-model="activePermissionKey" placeholder="输入 Active 公钥"></v-input>
					
				</v-col>
			</v-row>
			<v-row v-if="advancedMode">
				<v-col class="col-padding-right" span="8" align="left">
					<h4 class="top12 bottom6">购买 RAM (KB)</h4>
					<v-input-number class="input-small" size="large" :min="1" :step="1" v-model="ramBytes" placeholder="1"></v-input-number>
				</v-col>
				<v-col class="col-padding-right" span="8" align="left">
					<h4 class="top12 bottom6">抵押 NET (BOS)</h4>
					<v-input-number class="input-small" size="large" :min="0.001" :step="0.001" v-model="net" placeholder="0.003"></v-input-number>
				</v-col>
				<v-col span="8" align="left">
					<h4 class="top12 bottom6">抵押 CPU (BOS)</h4>
					<v-input-number class="input-small" size="large" :min="0.01" :step="0.01" v-model="cpu" placeholder="0.03"></v-input-number>
				</v-col>
			</v-row>
			<v-row>
				<v-col span="24" align="right" class="top12">
					<div class="config" @click="switchModeClick">高级设置<v-icon :type="advancedMode?'up':'down'"></v-icon></div>
				</v-col>
			</v-row>
			<v-row>
				<v-col span="24" align="center" class="top12">
					<v-button v-if="loginAccountName == ''" type="primary" size="large" @click="scatterLoginClick">登录</v-button>
					<v-button v-if="loginAccountName != ''" type="primary" size="large" @click="newAccountCreateClick">立即创建</v-button>
				</v-col>
			</v-row>
			
			<v-row>
				<!-- <v-col span="24" align="center">
					<div class="key-generate">公私钥生成器</div>
				</v-col> -->
			</v-row>
		</div>
		
	</div>
</template>

<script>
	
	import Eos from 'eosjs'
	
	export default {
		name:"newAccount",
		data() {
			return {
				loginAccountName:'',
				loginAccountAuthority:'active',
				newAccountName:'',
				ownerPermissionKey:'',
				activePermissionKey:'',
				ramBytes: "4",
				net: "0.003",
				cpu:"0.03",
				advancedMode:false,
				network:null
			}
		},
		methods: {
			switchModeClick() {
				this.advancedMode = !this.advancedMode;
			},
			scatterConnecting(network){
				var that = this;
				ScatterJS.connect('BOSNewAccount', {network} ).then(function(connected){
							if(connected){
								that.scatterLoginClick();
							}else{
								that.$message.warning("Scatter connect failed!", 2);
							}
					}).catch(function(e){
						that.$message.error(JSON.stringify(e), 2);
					});
			},
			scatterLoginClick(){
				var that = this;
				ScatterJS.login().then(function(result){
					const account = ScatterJS.identity.accounts.find(x => {
						return x.blockchain === 'eos';
					});
					that.loginAccountName = account.name;
					that.loginAccountAuthority = account.authority;
					console.log(JSON.stringify(result))
				}).catch(function(e){
					that.$message.error(JSON.stringify(e),2);
				});
			},
			newAccountCreateClick(){
				if(this.newAccountName.length != 12){
					this.$message.error('请输入有效用户名',2);
					return;
				}
				if(this.ownerPermissionKey.length != 53){
					this.$message.error('请输入有效 Owner 公钥',2);
					return;
				}
				if(this.activePermissionKey.length > 0 && this.activePermissionKey.length != 53){
					this.$message.error('请输入有效 Active 公钥',2);
					return;
				}
				if(this.activePermissionKey.length != 53){
					this.activePermissionKey = this.ownerPermissionKey;
				}
				var cpuString = (new Number(this.cpu)).toFixed(4).toString()+" BOS";
				var netString = (new Number(this.net).toFixed(4).toString())+" BOS";
			
				var that = this;
				
				const eos = ScatterJS.scatter.eos(that.network, Eos);
				eos.transaction({
					actions:[
						{
							account:'eosio',
							name:'newaccount',
							authorization: [{
							  actor: that.loginAccountName,
							  permission: that.loginAccountAuthority
							}],
							data:{
								owner:{
									threshold:1,
									waits:[],
									accounts:[],
									keys:[
										{
											key:that.ownerPermissionKey,
											weight:1
										}
									],
								},
								active:{
									threshold:1,
									waits:[],
									accounts:[],
									keys:[
										{
											key:that.activePermissionKey,
											weight:1
										}
									],
								},
								creator:that.loginAccountName,
								name:that.newAccountName
							}
						},
						{
							account:'eosio',
							name:'buyrambytes',
							authorization: [{
							  actor: that.loginAccountName,
							  permission: that.loginAccountAuthority
							}],
							data:{
								bytes:that.ramBytes*1024,
								payer:that.loginAccountName,
								receiver:that.newAccountName
							}
						},
						{
							account:'eosio',
							name:'delegatebw',
							authorization: [{
							  actor: that.loginAccountName,
							  permission: that.loginAccountAuthority
							}],
							data:{
								from:that.loginAccountName,
								receiver:that.newAccountName,
								transfer:0,
								stake_cpu_quantity:''+cpuString,
								stake_net_quantity:''+netString
							}
						}
					],
					}).then(function(result){
						that.$message.info("创建成功！！！",2);
					}).catch(function(e){
						that.$message.error(JSON.stringify(e),2);
					});
			}
		},
		mounted() {
			var network = ScatterJS.Network.fromJson({
								blockchain:'eos',
								chainId:'d5a3d18fbb3c084e3b1f3fa98c21014b5f3db536cc15d08f9f6479517c6a3d86',
								host:'bosnet.medishares.net',
								port:443,
								protocol:'https'
						});
			this.network = network;
			this.scatterConnecting(network);
		}
	}
</script>

<style scoped>
.title{
	padding-top: 16px;
	color: white;
}
.subtitle{
	padding-bottom: 16px;
	color: lightgray;
}
.panel{
	border-radius: 6px;
	margin: 0px 12px;
	padding: 8px;
	background: white;
}
h4{
	color: black;
}
.input {
	border-radius: 6px;
	color: black;
	font-size: larger;
	height: 42px;
}
.input-small {
	border-radius: 6px;
	color: black;
	font-size: larger;
}
.top12 {
	margin-top: 12px;
}
.bottom6 {
	margin-bottom: 6px;
}
.col-padding-left{
	padding-left: 6px;
}
.col-padding-right{
	padding-right: 6px;
}
.config{
	padding: 3px 0px;
	color: dimgray;
	font-size: x-small;
}
.key-generate{
	margin-top: 12px;
	padding: 6px;
	color: red;
}
</style>
