<template>
	<div class="token">
		<v-row>
			<v-col span="24" align="center">
				<v-radio-group type="button" size="large" v-model="value" :data="options"></v-radio-group>
			</v-col>
		</v-row>
		<v-row>
			<v-col span="24" align="left">
				<h3 class="top12 text-white"> 合约账户名 </h3>
				<v-input class="text-black" size="large" v-model="tokenAccount" placeholder="输入合约账户名,如 eosio"></v-input>
			</v-col>
		</v-row>
		<v-row>
			<v-col span="24" align="left">
				<h3 class="top12 text-white" > Symbol </h3>
				<v-input class="text-black" size="large" v-model="tokenSymbol" placeholder="输入 Symbol,如 MDS"></v-input>
			</v-col>
		</v-row>
		<v-row>
			<v-col span="24" align="center" class="top24">
				<v-button class="large-button" type="primary" size="large" @click="tokenQueryClick" :loading="loading">{{loading?"查询中...":"查询"}}</v-button>
			</v-col>
		</v-row>
		
		<v-row v-if="showResult">
			<v-col span="24" align="left" class="top12 result">
				<div class="left12"><h3>发行量:</h3>{{supply}}</div>
				<div class="left12"><h3>最大发行量:</h3>{{max_supply}}</div>
				<div class="left12"><h3>发行账户:</h3>{{issuer}}</div>
				<div class="left12"><h3>合约:</h3>{{tokenAccount}}</div>
				<div class="left12"><h3>Symbol:</h3>{{tokenSymbol}}</div>
				<div class="left12"><h3>精度:</h3>{{precision}}</div>
			</v-col>
		</v-row>
		
	</div>
</template>

<script>
	export default {
		name:"token",
		data() {
			return {
				options: [
					{value: 'EOS', text: 'EOS'},
					{value: 'ENU', text: 'ENU'},
					{value: 'BOS', text: 'BOS'},
					{value: 'Telos', text: 'Telos'}
				],
				value:'EOS',
				tokenAccount:'medisharesbp',
				tokenSymbol:'EMDS',
				loading:false,
				showResult:false,
				supply:"",
				max_supply:"",
				issuer:"",
				precision:4
			}
		},
		methods: {
			getURL(value){
				switch (value){
					case 'EOS':
						return "https://eosmainnet.medishares.net";
					case 'ENU':
						return "https://enu.medishares.net";
					case 'BOS':
						return "https://bos.medishares.net";
					default:
						return "https://telos.medishares.net";
						break;
				}
			},
			tokenQueryClick() {
				// Check
				if(this.tokenAccount.length <= 0 || this.tokenAccount.length > 12){
					this.$message.error("无效合约账户", 2);
					return;
				}
				if(this.tokenSymbol.length <= 0){
					this.$message.error("无效 Symbol", 2);
					return;
				}
				
				// Query
				this.loading = true;
				
				var url = this.getURL(this.value)+'/v1/chain/get_table_rows';
				
				var that = this;
				this.$http({
						method:'post',
						url:url,
						data:{
							code:that.tokenAccount.toLocaleLowerCase(),
							scope:that.tokenSymbol.toLocaleUpperCase(),
							table:"stat",
							json:true
						},
						headers:{
							'Access-Control-Allow-Origin':'*'
						}
					}).then(function(response){
						that.loading = false;
						if(response.data && response.data.rows){
							that.showResult = true;
							
							that.showResult = true;
							that.supply = response.data.rows[0].supply;
							that.max_supply = response.data.rows[0].max_supply;
							that.issuer = response.data.rows[0].issuer;
							
							var supply_amount = that.supply.split(" ")[0];
							that.precision = supply_amount.split(".")[1].length
						}else{
							that.showResult = false;
							that.$message.warning("无查询结果！", 2);
						}
					}).catch(function(e){
						that.loading = false;
						that.showResult = false;
						that.$message.error("无查询结果！", 2);
					})
			}
		},
	}
</script>

<style scope>
.token {
	padding: 12px;
}
.left12{
	padding-left: 12px;
	padding-bottom: 8px;
}
.top12{
	padding-top: 12px;
}
.top24{
	padding-top: 24px;
}
.text-white{
	color: white;
}
.text-black{
	color: black;
	font-size: xx-large;
}
.large-button{
	width: 100%;
}
.result{
	border-radius: 4px;
	background-color: white;
	margin-top: 12px;
	padding-left: 12px;
	padding-bottom: 12px;
}
</style>
